import { create } from "zustand";

const useAuthStore = create((set, get) => ({
  // === 인증 상태 ===
  isAuthenticated: true, // 목업: 기본 로그인 상태
  user: {
    id: 1,
    email: "mock@example.com",
    name: "Mock User",
    role: "USER",
  },
  isLoading: false,
  error: null,

  // === 로그인 폼 상태 ===
  email: "",
  password: "",
  emailError: "",
  passwordError: "",

  // === 비밀번호 재설정 상태 ===
  verificationCode: "",
  newPassword: "",
  confirmPassword: "",
  isCodeSent: false,
  verificationCodeError: "",
  newPasswordError: "",
  confirmPasswordError: "",

  // === 인증 액션 ===
  setLoading: (loading) => set({ isLoading: loading }),
  setError: (error) => set({ error }),
  clearError: () => set({ error: null }),
  setUser: (user) =>
    set({
      isAuthenticated: true,
      user,
      isLoading: false,
      error: null,
    }),

  // 로그인 (목업)
  login: async (email, password) => {
    set({ isLoading: true, error: null });

    // 심플한 목업 로그인 지연
    await new Promise((resolve) => setTimeout(resolve, 500));

    const mockUser = {
      id: 1,
      email: email,
      name: "Mock User",
      role: "USER",
    };

    set({
      isAuthenticated: true,
      user: mockUser,
      isLoading: false,
      error: null,
    });
    return { success: true, user: mockUser };
  },

  // 로그아웃 (목업)
  logout: async () => {
    set({ isLoading: true });
    await new Promise((resolve) => setTimeout(resolve, 300));

    set({
      isAuthenticated: false,
      user: null,
      isLoading: false,
      error: null,
    });
    return { success: true };
  },

  // 프로필 로드 (목업)
  loadProfile: async () => {
    set({ isLoading: true, error: null });
    await new Promise((resolve) => setTimeout(resolve, 300));

    const profile = {
      id: 1,
      name: "Mock User",
      email: "mock@example.com",
      nickName: "Mock Nickname",
      imageKey: "https://api.dicebear.com/9.x/avataaars/svg?seed=Felix",
    };

    set({
      isAuthenticated: true,
      user: profile,
      isLoading: false,
    });
    return { success: true, profile };
  },

  // === 로그인 폼 액션 ===
  setEmail: (email) => set({ email }),
  setPassword: (password) => set({ password }),
  setEmailError: (error) => set({ emailError: error }),
  setPasswordError: (error) => set({ passwordError: error }),

  // === 비밀번호 재설정 액션 ===
  setVerificationCode: (code) => set({ verificationCode: code }),
  setNewPassword: (password) => set({ newPassword: password }),
  setConfirmPassword: (password) => set({ confirmPassword: password }),
  setVerificationCodeError: (error) => set({ verificationCodeError: error }),
  setNewPasswordError: (error) => set({ newPasswordError: error }),
  setConfirmPasswordError: (error) => set({ confirmPasswordError: error }),
  setCodeSent: (sent) => set({ isCodeSent: sent }),

  // === 초기화 액션 ===
  resetState: () =>
    set({
      isAuthenticated: false,
      user: null,
      isLoading: false,
      error: null,
      email: "",
      password: "",
      emailError: "",
      passwordError: "",
      verificationCode: "",
      newPassword: "",
      confirmPassword: "",
      isCodeSent: false,
      verificationCodeError: "",
      newPasswordError: "",
      confirmPasswordError: "",
    }),

  resetErrors: () =>
    set({
      error: null,
      emailError: "",
      passwordError: "",
      verificationCodeError: "",
      newPasswordError: "",
      confirmPasswordError: "",
    }),

  resetForm: () =>
    set({
      email: "",
      password: "",
      emailError: "",
      passwordError: "",
    }),

  resetPasswordResetState: () =>
    set({
      verificationCode: "",
      newPassword: "",
      confirmPassword: "",
      isCodeSent: false,
      verificationCodeError: "",
      newPasswordError: "",
      confirmPasswordError: "",
    }),

  // === 초기화 (앱 시작 시 로그인 상태 확인 - 목업은 항상 true 또는 로컬 스토리지 체크 흉내) ===
  initialize: () => {
    // 목업: 항상 로그인 상태 유지하거나, 필요 시 false로 변경 가능
    // 여기서는 기본값을 true로 설정해둠
  },
}));

export default useAuthStore;
